/**
 *  - 
 */
import React, { useState, useEffect, useMemo } from 'react';
import { TrendingUp, RefreshCw, Calendar, BarChart3, ChevronLeft, ChevronRight } from 'lucide-react';
import axios from 'axios';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Cell } from 'recharts';
import { API_BASE_URL, COLORS } from '../../constants';
import { formatDate } from '../../utils';

export default function HotSpotsModule({ boardType, selectedPeriod, topN, selectedDate }) {
  const [analysisData, setAnalysisData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(10); // 10
  const [top1000Industry, setTop1000Industry] = useState(null);
  const [sortBy, setSortBy] = useState('hit_count'); // 'hit_count', 'rank', 'price_change'

  // 
  useEffect(() => {
    const fetchData = async () => {
      if (!selectedPeriod || !boardType || !topN || !selectedDate) return;
      
      setLoading(true);
      setError(null);
      setAnalysisData(null);
      try {
        let url = `${API_BASE_URL}/api/analyze/${selectedPeriod}?board_type=${boardType}&top_n=${topN}`;
        if (selectedDate) {
          url += `&date=${selectedDate}`;
        }
        const response = await axios.get(url);
        setAnalysisData(response.data);
      } catch (err) {
        console.error(':', err);
        const errorMsg = err.code === 'ERR_NETWORK' 
          ? ''
          : (err.response?.data?.detail || '');
        setError(errorMsg);
        setAnalysisData(null);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [selectedPeriod, boardType, topN, selectedDate]);

  // 1000
  useEffect(() => {
    const fetchTop1000Industry = async () => {
      if (!selectedDate) return;
      try {
        let url = `${API_BASE_URL}/api/industry/top1000?limit=1000`;
        if (selectedDate) {
          url += `&date=${selectedDate}`;
        }
        const response = await axios.get(url);
        setTop1000Industry(response.data);
      } catch (err) {
        console.error('1000:', err);
      }
    };
    fetchTop1000Industry();
  }, [selectedDate]);


  // 
  const industryStats = useMemo(() => {
    if (!analysisData || !analysisData.stocks) return [];
    
    const industryCount = {};
    analysisData.stocks.forEach(stock => {
      const industry = stock.industry || '';
      industryCount[industry] = (industryCount[industry] || 0) + 1;
    });
    
    return Object.entries(industryCount)
      .map(([name, value]) => ({ name, value }))
      .sort((a, b) => b.value - a.value);
  }, [analysisData]);

  // 
  const sortedStocks = useMemo(() => {
    if (!analysisData || !analysisData.stocks || !Array.isArray(analysisData.stocks)) return [];
    
    // 
    const stocksWithFixedRank = [...analysisData.stocks]
      .sort((a, b) => (b.appearances || 0) - (a.appearances || 0)) //  appearances 
      .map((stock, index) => ({
        ...stock,
        fixed_rank: index + 1 // 
      }));
    
    // sortBy
    switch (sortBy) {
      case 'rank':
        return stocksWithFixedRank.sort((a, b) => (a.latest_rank || 0) - (b.latest_rank || 0)); // 
      case 'price_change':
        return stocksWithFixedRank.sort((a, b) => (b.price_change || 0) - (a.price_change || 0)); // 
      case 'hit_count':
      default:
        return stocksWithFixedRank; // 
    }
  }, [analysisData, sortBy]);

  // 
  const paginatedStocks = useMemo(() => {
    if (!sortedStocks || sortedStocks.length === 0) return [];
    const start = (currentPage - 1) * pageSize;
    const end = start + pageSize;
    return sortedStocks.slice(start, end);
  }, [sortedStocks, currentPage, pageSize]);

  // 
  const totalPages = useMemo(() => {
    if (!analysisData || !analysisData.stocks) return 0;
    return Math.ceil(analysisData.stocks.length / pageSize);
  }, [analysisData, pageSize]);

  // 
  useEffect(() => {
    setCurrentPage(1);
  }, [selectedPeriod, boardType, sortBy]);

  return (
    <>
      {/* Error Messages */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
          <p className="text-red-800 font-medium">: {error}</p>
        </div>
      )}

      {/* Analysis Results */}
      {analysisData && !loading && (
        <div className="bg-white rounded-lg shadow-md overflow-hidden mb-6">
          {/* Stats Header */}
          <div className="bg-gradient-to-r from-indigo-600 to-purple-600 px-6 py-4">
            <div className="flex items-center justify-between text-white">
              <div className="flex items-center space-x-3">
                <BarChart3 className="h-6 w-6" />
                <h2 className="text-xl font-bold">
                  {analysisData.period} 
                </h2>
              </div>
              <div className="text-lg font-semibold">
                 {analysisData.total_stocks} 
              </div>
            </div>
            <p className="text-indigo-100 text-sm mt-2">
              : {analysisData.analysis_dates.map(formatDate).join(', ')}
            </p>
          </div>

          {/*  */}
          <div className="px-6 py-4 bg-gray-50 border-b border-gray-200">
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-600 font-medium">:</span>
              <button
                onClick={() => setSortBy('hit_count')}
                className={`px-3 py-1 rounded text-sm transition-colors ${
                  sortBy === 'hit_count'
                    ? 'bg-indigo-600 text-white font-medium'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                
              </button>
              <button
                onClick={() => setSortBy('rank')}
                className={`px-3 py-1 rounded text-sm transition-colors ${
                  sortBy === 'rank'
                    ? 'bg-indigo-600 text-white font-medium'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                
              </button>
              <button
                onClick={() => setSortBy('price_change')}
                className={`px-3 py-1 rounded text-sm transition-colors ${
                  sortBy === 'price_change'
                    ? 'bg-indigo-600 text-white font-medium'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                
              </button>
            </div>
          </div>

          {/* Table */}
          {analysisData.stocks && Array.isArray(analysisData.stocks) && analysisData.stocks.length > 0 ? (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {paginatedStocks.map((stock, index) => (
                    <tr
                      key={stock.stock_code}
                      className="hover:bg-gray-50 transition-colors"
                    >
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {stock.fixed_rank}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className="text-sm font-semibold text-indigo-600">
                          {stock.stock_code}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className="text-sm font-medium text-gray-900">
                          {stock?.stock_name || stock?.name || '-'}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className="text-xs text-gray-600 bg-purple-50 px-2 py-1 rounded">
                          {stock.industry}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                          {stock.appearances} 
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                           {stock.latest_rank} 
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-right">
                        {stock.price_change !== null && stock.price_change !== undefined ? (
                          <span className={`text-sm font-medium ${
                            stock.price_change > 0 ? 'text-red-600' : 
                            stock.price_change < 0 ? 'text-green-600' : 
                            'text-gray-600'
                          }`}>
                            {stock.price_change > 0 ? '+' : ''}{stock.price_change.toFixed(2)}%
                          </span>
                        ) : (
                          <span className="text-sm text-gray-400">-</span>
                        )}
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">
                        {stock.date_rank_info && Array.isArray(stock.date_rank_info) && stock.date_rank_info.length > 0
                          ? stock.date_rank_info.map((info, idx) => (
                              <div key={idx} className="text-xs">
                                {formatDate(info.date)}({info.rank})
                              </div>
                            ))
                          : '-'
                        }
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <div className="px-6 py-12 text-center text-gray-500">
              <BarChart3 className="mx-auto h-12 w-12 text-gray-400 mb-3" />
              <p className="text-lg font-medium"></p>
              <p className="text-sm mt-1"></p>
            </div>
          )}
          
          {/* Pagination */}
          <div className="px-6 py-4 border-t border-gray-200 flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-700">
                 {(currentPage - 1) * pageSize + 1} - {Math.min(currentPage * pageSize, analysisData.total_stocks)}  {analysisData.total_stocks} 
              </span>
            </div>
            <div className="flex items-center space-x-4">
              {/* Page Size Selector */}
              <div className="flex items-center space-x-2">
                <span className="text-sm text-gray-700">:</span>
                <select
                  value={pageSize}
                  onChange={(e) => {
                    setPageSize(Number(e.target.value));
                    setCurrentPage(1);
                  }}
                  className="border border-gray-300 rounded px-2 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500"
                >
                  <option value={10}>10</option>
                  <option value={20}>20</option>
                  <option value={30}>30</option>
                  <option value={50}>50</option>
                </select>
              </div>
              
              {/* Page Navigation */}
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => setCurrentPage(Math.max(1, currentPage - 1))}
                  disabled={currentPage === 1}
                  className="p-1 rounded border border-gray-300 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <ChevronLeft className="h-5 w-5" />
                </button>
                <span className="text-sm text-gray-700">
                  {currentPage} / {totalPages}
                </span>
                <button
                  onClick={() => setCurrentPage(Math.min(totalPages, currentPage + 1))}
                  disabled={currentPage >= totalPages}
                  className="p-1 rounded border border-gray-300 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <ChevronRight className="h-5 w-5" />
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Industry Bar Chart */}
      {analysisData && analysisData.stocks && analysisData.stocks.length > 0 && !loading && (
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <div className="flex items-center space-x-2 mb-4">
            <BarChart3 className="h-5 w-5 text-indigo-600" />
            <h3 className="text-lg font-bold text-gray-900"> ({analysisData.stocks.length})</h3>
          </div>
          {industryStats && industryStats.length > 0 ? (
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={industryStats}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" angle={-45} textAnchor="end" height={100} />
                <YAxis />
                <Tooltip formatter={(value) => [`${value}`, '']} />
                <Bar dataKey="value" fill="#8884d8">
                  {industryStats.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          ) : (
            <div className="text-center py-8 text-gray-500">
              <p></p>
            </div>
          )}
        </div>
      )}

      {/* Loading State */}
      {loading && (
        <div className="bg-white rounded-lg shadow-md p-12 text-center mb-6">
          <RefreshCw className="mx-auto h-12 w-12 text-indigo-600 animate-spin mb-4" />
          <p className="text-gray-600 text-lg">...</p>
        </div>
      )}
    </>
  );
}
